.special {
  padding-top: 68px;
  background-color: var(--light);

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__wrap {
    @include flex-all-sb;
    margin-bottom: 26px;

    @include tablet {
      margin-bottom: 29px;
    }

    @include small-tablet {
      margin-bottom: 26px;
    }
  }

  &__title {
    line-height: 105%;
  }

  &__tab-block {
    display: flex;
    gap: 13px;

    @include small-tablet {
      display: none;
    }
  }

  &__list {
    display: flex;
    width: 200%;
    gap: 32px;
    transition: all 1s;
  }

  &__list > .special__card:nth-child(2) {
    width: 24.4%;

    @media (max-width: (320px)) {
      padding-top: 111px;
    }
  }

  &__list > .special__card:nth-child(3) {
    @media (max-width: (320px)) {
      padding-top: 62px;
    }
  }

  &__list > .special__card:nth-child(5) {
    @media (max-width: (320px)) {
      padding-top: 118px;
    }
  }

  &__list > .special__card:nth-child(6) {
    @media (max-width: (320px)) {
      padding-top: 63px;
    }
  }

  &__slide {
    width: 23.13%;
    height: auto;

    @include tablet {
      width: 31.1%;
    }

    @include small-tablet {
      width: 47.5%;
    }

    @media (max-width: (480px)) {
      max-width: 466px;
    }
  }

  &__card {
    width: 100%;
    height: 100%;


    @media (max-width: (480px)) {
      padding-top: 72px;
    }

    @media (max-width: (320px)) {
      padding-bottom: 33px;
    }
  }

  &__card_name_chair {
    padding-top: 72px;
  }

  &__card_name_sofa {
    padding-top: 37px;
    background-image: url('../img/sofa-parsing.svg');
    background-repeat: no-repeat;
    background-position: 92.5% 92.5%;

    @include tablet {
      padding-top: 112px;
      background-image: none;
    }
  }

  &__card_name_clock {
    padding-top: 64px;
  }

  &__card_name_deer {
    padding-top: 72px;
  }

  &__card_name_sofa-brown {
    padding-top: 120px;
  }

  &__card_name_termometer {
    padding-top: 64px;
  }

  &__image_name_sofa {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 48px;
    }

    @media (max-width: 480px) {
      padding-left: 0;
    }

    @media (max-width: (320px)) {
      margin-bottom: 50px;
      padding-right: 0;
    }
  }

  &__inner_name_sofa {
    padding-right: 5.14%;
    padding-left: 5.14%;

    @include tablet {
      padding-left: 10.14%;
    }

    @media (max-width: (320px)) {
      padding-left: 0.14%;
    }
  }

  &__content {
    margin-bottom: 21px;

    @include tablet {
      margin-bottom: 25px;
    }

    @media (max-width: (320px)) {
      align-items: flex-start;
      padding-left: 30px;
      margin-bottom: 26px;
      width: 100%;
    }
  }

  &__content_name_sofa-brown {
    padding-top: 40px;
  }

  &__subtitle {
    margin-bottom: 23px;

    @include tablet {
      margin-bottom: 19px;
    }

    @media (max-width: (320px)) {
      text-align: left;
      font-size: 24px;
    }
  }

  &__price {
    margin-right: 26px;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;
    color: var(--secondary-background);
  }

  &__old-price {
    @media (max-width: (970px)) {
      font-size: 12px;
    }

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__wrap-btn {

    @media (max-width: (320px)) {
      align-self: flex-start;
      padding-left: 30px;
    }
  }

  &__btn {
    padding: 19px 34px;

    @include tablet {
      padding: 17px 34px;
    }
  }

  &__discount-total {
    position: absolute;
    top: 28px;
    left: -1px;
    display: block;
    padding-top: 7px;
    box-sizing: border-box;
    min-width: 93px;
    height: 34px;
    text-align: center;
    color: var(--light);
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    background-image: url('../img/bg.svg');
    background-repeat: no-repeat;
    background-size: cover;

    @include small-tablet {
      padding-right: 6px;
    }

    @media (max-width: (320px)) {
      top: 30px;
    }
  }

  @include small-tablet {
    padding-top: 69px;
  }

  @media (max-width: (320px)) {
    padding-top: 71px;
  }
}

.special__slide_number_2 {
  width: 48.75%;

  @include tablet {
    width: 31.1%;
  }

  @include small-tablet {
    width: 100%;
  }
}
