.footer {
  padding-top: 25px;
  background-color: var(--md_grey-footer);

  &__container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 29px;

    @include tablet {
      margin-bottom: 24px;
    }

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__left {
    width: 18%;

    @include tablet {
      width: 19.6%;
    }

    @include small-tablet {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    @media (max-width: (480px)) {
      align-items: center;
      flex-direction: column;
      margin-bottom: 24px;
    }
  }

  &__logo {
    display: block;
    margin-bottom: 27px;

    @include tablet {
      margin-bottom: 26px;
    }

    @media (max-width: (320px)) {
      margin-bottom: 22px;
    }
  }

  &__social-list {
    display: flex;
    gap: 20px;

    @include small-tablet {
      gap: 20px;
      padding-right: 50px;
    }

    @media (max-width: (320px)) {
      padding-right: 0px;
    }
  }

  &__social-link {
    display: block;
    width: 30px;
    height: 30px;

    & svg {
      width: 100%;
      height: 100%;
    }

    &:focus {
      outline: 3px solid #C998FB;
      background-color: #C998FB;
    }

    &:focus svg > use {
      color: #FFFFFF;
    }

    &:hover svg > use {
      color: var(--primary-btn);
    }

    &:active {
      background-color: transparent;
    }

    &:active svg > use {
      color: #7033AC;
    }
  }

  &__social-link svg > use {
    color: #999999;
    transition: all 0.4s ease-in-out;
  }

  &__nav {
    display: flex;
    width: 66%;

    @include tablet {
      width: 77%;
    }

    @include small-tablet {
      width: 100%;
    }

    @media (max-width: (480px)) {
      flex-wrap: wrap;
    }

    @media (max-width: (320px)) {
      gap: 2%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 6px;
    color: var(--dark);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;

    @include small-tablet {
      margin-bottom: 4px;
    }
  }

  &__nav-list {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  &__nav-link {
    color: var(--grey);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    transition: background-color .5s ease-in-out, color .5s ease-in-out;

    &:focus {
      @include focus;
    }


    @include hover {
      color: #A65CF0;
    }


    &:active {
      @include active;
    }
  }

  &__nav-about-us {
    margin-right: 13.5%;

    @include tablet {
      margin-right: 10.1%;
    }

    @include small-tablet {
      margin-right: 6.1%;
    }

    @media (max-width: (480px)) {
      margin-right: 0;
      width: 63%;
    }

    @media (max-width: (320px)) {
      width: 51%;
    }
  }

  &__nav-services {
    margin-right: 15.8%;

    @include tablet {
      margin-right: 13%;
    }

    @include small-tablet {
      margin-right: 0%;
      width: 89.1%;
    }

    @media (max-width: (480px)) {
      width: 37%;
      margin-bottom: 24px;
    }

    @media (max-width: (320px)) {
      width: 46%;
    }
  }

  &__nav-connect {
    margin-right: 17.1%;

    @include tablet {
      margin-right: 14.7%;
    }

    @include small-tablet {
      margin-right: 4.7%;
      width: 70%;
    }

    @media (max-width: (480px)) {
      margin-right: 0;
      width: 58%;
    }

    @media (max-width: (320px)) {
      width: 51%;
    }
  }

  &__nav-cooperation {

    @media (max-width: (480px)) {
      width: 42%;
    }

    @media (max-width: (320px)) {
      width: 47%;
    }
  }

  &__trademark {
    display: block;
    height: 30px;
    color: var(--grey);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background-color: var(--grey_light-background);

    @include tablet {
      padding-top: 5px;
    }

    @media (max-width: (320px)) {
      padding-top: 8px;
    }
  }

  @include tablet {
    padding-top: 28px;
  }

  @include small-tablet {
    padding-top: 22px;
  }

  @media (max-width: (320px)) {
    padding-top: 24px;
  }
}
