.cooperation {

  &__container {

    @include small-tablet {
      padding-top: 9px;
      padding-right: 9.4%;
      padding-left: 9.4%;
    }

    @media (max-width: (320px)) {
      padding-top: 18px;
      padding-right: 12px;
      padding-left: 12px;
    }
  }

  &__title {
    margin-bottom: 32px;
  }

  &__suppliers {
    margin-bottom: 32px;
    max-width: 842px;

    @media (max-width: (320px)) {
      margin-bottom: 32px;
    }
  }

  &__get {
    margin-bottom: 32px;
    max-width: 624px;
  }

  &__benefits {
    margin-bottom: 32px;
    max-width: 850px;

    @include small-tablet {
      margin-bottom: 30px;
    }

    @media (max-width: (320px)) {
      margin-bottom: 35px;
    }
  }

  &__guarantees {
    padding-bottom: 93px;
    max-width: 824px;
    background-position: 0 89%;
    background-repeat: no-repeat;
    background-size: 77px 116px;
    background-image: url("/img/pencil.webp");

    @include tablet {
      padding-bottom: 91px;
      background-size: 78px 116px;
      background-position: 0 88%;
    }

    @include small-tablet {
      padding-bottom: 95px;
      background-position: 0 90%;
    }

    @media (max-width: (480px)) {
      background-size: 45px 71px;
      background-position: 0 92%;
    }

    @media (max-width: (320px)) {
      padding-bottom: 70px;
      background-size: 34px 54px;
      background-position: 0 93.5%;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 12px;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &--offset {
      margin-bottom: 12px;
    }

    @media (max-width: (320px)) {
      margin-bottom: 13px;
      font-size: 16px;
    }

    &_size {
      @media (max-width: (320px)) {
        margin-bottom: 11px;
        font-size: 14px;
      }
    }
  }

  &__subtitle_bold {
    margin-bottom: 0;
    padding-left: 109px;
    line-height: 150%;

    @media (max-width: (480px)) {
      padding-left: 63px;
      font-size: 20px;
    }

    @media (max-width: (320px)) {
      padding-left: 52px;
      font-size: 16px;
    }
  }

  &__descr {
    margin: 0;
    color: var(--grey_price);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 182%;

    @media (max-width: (320px)) {
      font-size: 14px;
      line-height: 177%;
    }
  }

  &__item {
    margin-bottom: 12px;
    background-position: top left;
    background-repeat: no-repeat;
    background-size: 25px 25px;

    &_network {
      background-image: url("/img/network.svg");
    }

    &_briefcase {
      background-image: url("/img/briefcase.svg");
    }

    &_git-pull-request {
      background-image: url("/img/git-pull-request.svg");
    }

    &_guarantees {
      padding-left: 8px;
    }

    @media (max-width: (320px)) {

      margin-bottom: 14px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__text {
    margin: 0;
    padding-left: 37px;
    color: var(--grey_price);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

    @media (max-width: (320px)) {
      font-size: 14px;
      line-height: 148%;
    }
  }

  &__headline {
    margin: 0;
    margin-bottom: 12px;
    color: var(--secondary-background);
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    @media (max-width: (320px)) {
      margin-bottom: 13px;
    }
  }

  &__list_guarantees {
    padding: 0;
    margin: 0;
    margin-bottom: 51px;
    margin-left: 18px;
    width: 100%;
    color: var(--secondary-background);

    @media (max-width: (320px)) {
      margin-bottom: 33px;
      width: 94%;
    }
  }
}
