.card {

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    padding-bottom: 30px;
    border-radius: 10px;
    background-color: var(--grey_light-background);

    &_name_R94 {
      @media (max-width: (320px)) {
        padding-top: 54px;
      }
    }

    &_name_F85 {

      @include small-tablet {
        display: none;
      }
    }

    &_name_V43 {
      @include small-tablet {
        display: none;
      }
    }
  }

  &__link {
    position: absolute;
    inset: 0;
    z-index: 1;
  }

  &__image {
    margin-bottom: 16px;
    padding: 0 15px;
    text-align: center;
    font-size: 0;

    @include tablet {
      margin-bottom: 16px;
    }

    @media (max-width: (320px)) {
      padding: 0;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    padding-right: 10.14%;
    padding-left: 10.14%;

    @include small-tablet {
      padding-right: 9.14%;
    }

    @media (max-width: (320px)) {
      padding: 0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    margin-bottom: 25px;
    width: 100%;
    text-align: left;

    @include small-tablet {
      margin-bottom: 23px;
    }

    @media (max-width: (320px)) {
      align-items: flex-start;
      margin-bottom: 19px;
      width: 98%;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 24px;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 128%;

    @media (max-width: (480px)) {
      width: 80%;
      font-size: 16px;
    }

    @media (max-width: (320px)) {
      width: 90%;
    }
  }

  &__price {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;
    color: #666666;
  }

  &__old-price {
    color: var(--grey);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;
    text-decoration: line-through;
  }

  &__wrap-btn {
    align-self: flex-start;
    z-index: 2;
    margin-top: auto;
  }

  &__rate {
    position: absolute;
    top: 33px;
    left: 30px;
    color: var(--secondary-background);

    &::before {
      content: url('../img/star.svg');
      padding-right: 6px;
      width: 15px;
      height: 15px;

      @media (max-width: (320px)) {
        content: url('../img/star-320.svg');
      }
    }

    @include small-tablet {
      top: 35px;
    }

    @media (max-width: (320px)) {
      top: 18px;
      left: 16px;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 130%;
    }
  }
}
