.link {
  position: relative;
  padding-left: 30px;
  width: 150px;
  color: var(--primary-btn);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  transition: color .4s ease-in-out;

  &:focus {
    outline: none;
  }

  &:active {
    color: #7033AC;
  }

  &::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 29px;
    width: 81px;
    height: 0;
    opacity: 0;
    transition: all .4s ease-in-out;

    @include mobile {
      display: none;
    }
  }

  &:focus::after {
    outline: 1px solid #A65CF0;
    opacity: 1;
  }

  &:hover::after {
    outline: 1px solid #A65CF0;
    opacity: 1;
  }

  &:active::after {
    outline: 1px solid #7033AC;
    opacity: 1;
  }

  & svg {
    position: absolute;
    top: -1px;
    left: 116px;
    width: 23px;
    height: 23px;

    @include mobile {
      top: 4px;
      left: 0px;
    }
  }

  &:focus svg > use {
    --stroke: #A65CF0;
    color: #FFFFFF;
    --fill_none: #C998FB;
  }

  &:hover svg > use {
    --fill_none: #A65CF0;
    color: #FFFFFF;
  }

  &:active svg > use {
    --stroke: #7033AC;
    color: #FFFFFF;
    --fill_none: #7033AC;
  }


  @include mobile {
    width: 23px;
  }
}

.link svg > use {
  --stroke: #A65CF0;
  color: #A65CF0;
  --fill_none: none;
  transition: color .4s ease-in-out;
}
