.card-one {
  padding-bottom: 78px;

  &__container {

    @include small-tablet {
      padding-top: 9px;
    }

    @media (max-width: (320px)) {
      padding-top: 8px;
    }
  }

  &__item {
    &:nth-child(1) {
      &::after {
        left: 61px;
      }
    }

    &:nth-child(4) {
      &::after {
        left: 117px;
      }
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0.6fr 1.4fr;
    grid-template-columns: repeat(12, 1fr);
    row-gap: 108px;
    column-gap: 32px;
    padding-top: 16px;

    @include tablet {
      grid-template-columns: repeat(9, 1fr);
      row-gap: 83px;
      padding-top: 0;
    }

    @media (max-width: (970px)) {
      grid-template-rows: 1fr 1fr auto;
      row-gap: 0;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: 0.92fr 0.93fr 1fr auto;
    }

    @include mobile {
      grid-template-rows: 0.7fr 0.83fr 1fr auto;
    }

    @media (max-width: (480px)) {
      grid-template-rows: 0.6fr 0.78fr 1fr auto;
    }

    @media (max-width: (320px)) {
      grid-template-rows: 0.305fr 0.6fr 1fr auto;
      column-gap: 16px;
    }
  }

  &__head-image {
    grid-row: 1;
    grid-column: 6 span;

    @include tablet {
        grid-column: 5 span;
        padding-top: 24px;
    }

    @include small-tablet {
      grid-column: 6 span;
      padding-top: 0;
    }
  }

  &__block-descr {
    position: relative;
    grid-row: 1;
    grid-column: 7 / 6 span;
    display: flex;
    flex-direction: column;
    padding-top: 19px;

    @include tablet {
      grid-column: 6/4 span;
      padding-top: 23px;
    }

    @include small-tablet {
      grid-row: 2;
      grid-column: 6 span;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 5px;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;

    @media (max-width: (320px)) {
      margin-bottom: 7px;
      font-size: 16px;
    }
  }

  &__note {
    display: block;
    margin-bottom: 21px;
    color: var(--grey);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;

    @media (max-width: (320px)) {
      width: 80%;
      margin-bottom: 16px;
    }
  }

  &__price {
    display: block;
    margin-bottom: 21px;
    color: var(--dark);
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;

    @media (max-width: (320px)) {
      margin-bottom: 16px;
      font-size: 24px;
    }
  }

  &__btn {
    align-self: start;

    &--offset {
      margin-bottom: 12px;
      padding: 19px 35px;
      color: var(--light);
      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: 100%;

      @media (max-width: (320px)) {
        align-self: auto;
      }
    }

    &--add {
      padding-left: 30px;
      color: var(--primary-btn);
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 130%;

      @media (max-width: (320px)) {
        align-self: auto;
        padding-left: 0;
      }
    }
  }

  &__rate {
    position: absolute;
    top: 0;
    left: 0;
    color: var(--secondary-background);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;

    &::before {
      content: url('/img/star.svg');
      padding-right: 6px;
    }
  }

  &__mechanism-descr {
    grid-row: 2;
    grid-column: 6 span;

    @include tablet {
      grid-column: 5 span;
    }

    @media (max-width: (970px)) {
      grid-column: 9 span;
      margin-bottom: 50px;
    }

    @include small-tablet {
      grid-row: 3;
      grid-column: 5 span;
      margin-bottom: 26px;
    }

    @media (max-width: (320px)) {
      grid-column: 6 span;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 8px;
    color: var(--dark);
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
  }

  &__blocktext {
    margin-bottom: 105px;

    @include tablet {
      margin-bottom: 82px;
    }

    @media (max-width: (970px)) {
      margin-bottom: 40px;
    }

    @include small-tablet {
      margin-bottom: 33px;
    }
  }

  &__mechanism-text {
    margin: 0;
    color: var(--dark);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 180%;

    &:first-child {
      margin-bottom: 8px;
    }
  }

  &__characteristic {
    grid-row: 2;
    grid-column: 7 / 6 span;

    @include tablet {
      grid-column: 6/4 span;
    }

    @media (max-width: (970px)) {
      grid-column: 9 span;
      grid-row: 3;
    }

    @include small-tablet {
      grid-row: 4;
      grid-column: 6 span;
    }
  }

  &__characteristic-item {
    display: flex;
    width: 100%;
    height: 21px;

    &:not(:last-child) {
      margin-bottom: 4px;

      @media (max-width: (320px)) {
        margin-bottom: 8px;
      }
    }

    @include mobile {
      flex-direction: column;
      height: auto;
    }
  }

  &__characteristic-name {
    width: auto;
    overflow: hidden;
    color: var(--grey);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 180%;

    @include mobile {
      font-size: 12px;
    }

    @media (max-width: (320px)) {
      font-size: 10px;
      line-height: 140%;
    }
  }

  &__characteristic-dotted {
    flex-grow: 2;
    margin-bottom: 3px;
    border-bottom: 1px dotted;

    @include mobile {
      display: none;
    }
  }

  &__characteristic-value {
    width: 47.5%;
    text-align: start;
    color: var(--dark);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 180%;

    @include tablet {
      width: 53.5%;
    }

    @include small-tablet {
      width: 34.7%;
    }

    @include mobile {
      width: 100%;
    }

    @media (max-width: (320px)) {
      line-height: 136%;
    }
  }

    @include tablet {
      padding-bottom: 66px;
    }

    @include small-tablet {
      padding-bottom: 38px;
    }

    @media (max-width: (320px)) {
      padding-bottom: 70px;
    }
}
