.useful {
  padding-top: 70px;
  background-color: var(--light);

  &__container {
    @media (max-width: (575px)) {
      display: flex;
      flex-direction: column;
    }
  }

  &__wrap {
    @include flex-all-sb;
    margin-bottom: 26px;
  }

  &__tab-block {
    display: flex;
    gap: 13px;

    @media (max-width: (768px)) {
      display: none;
    }
  }

  &__slide {
    width: 48.75%;
    height: auto;
    display: none;
    box-sizing: border-box;
    border-radius: 10px;
    text-align: left;

    &:nth-of-type(1) {
      display: block;
    }
    &:nth-of-type(2) {
      display: block;
    }

    &:nth-of-type(3) {
      display: block;
    }


    @include tablet {
      width: 31.1%;

      &:nth-of-type(4) {
        display: block;
      }

      &:nth-of-type(5) {
        display: block;
      }
    }

    @media (max-width: (320px)) {
      height: 1px;
      min-height: 456px;
    }
  }

  &__list {
    display: none;

    @media (max-width: (480px)) {
      display: flex;
      flex-direction: column;
      gap: 24px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    height: 100%;

    &:nth-child(4) {
      @media (max-width: (480px)) {
        display: none;
      }

      @media (max-width: (320px)) {
        display: flex;
      }
    }

    &:nth-child(5) {
      @media (max-width: (480px)) {
        display: none;
      }

      @media (max-width: (320px)) {
        display: flex;
      }
    }
  }

  &__link {
    position: absolute;
    inset: 0;
    z-index: 1;
  }

  &__img {
    display: block;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 30px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    height: 100%;
    background-color: var(--grey_light-background);

    @include tablet {
      padding: 30px 29px;
      padding: 30px 29px 32px 29px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 36px;
    text-align: left;
    width: 96%;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 136%;

    @include tablet {
      margin-bottom: 32px;
      color: var(--dark);
      font-size: 16px;
      line-height: normal;
    }
  }

  &__btn {
    align-self: start;

    @include tablet {
      padding: 17px 35px;
    }
  }
}
