.nav-submenu {
  @include flex-v-center;
  background-color: #F6F6F6;


  &__list {
    display: flex;
    gap: 64px;

    @include small-tablet {
      gap: 35px;
    }
  }


  &__item {

    &:nth-child(3) {
      @media (max-width: (320px)) {
        width: 113px;
      }
    }
  }

  &__link {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    color: var(--grey);
    transition: background-color .5s ease-in-out, color .5s ease-in-out;

    &:focus {
      @include focus;
    }


    @include hover {
      color: #A65CF0;
    }


    &:active {
      @include active;
    }
  }

  @include tablet {
    margin-left: -55%;
    padding-left: 55%;
    min-height: 50px;
  }

  @include small-tablet {
    margin-right: -55%;
    padding-right: 55%;
  }

  @media (max-width: (480px)) {
    display: none;
  }


}

.nav-submenu-480 {
  display: none;
  margin-right: -55%;
  padding-right: 55%;
  background-color: #F6F6F6;

  &__list {
    display: flex;
    gap: 64px;

    @media (max-width: (480px)) {
      flex-wrap: wrap;
      justify-content: space-between;
      row-gap: 12px;
      margin-left: -13%;
      padding: 24px 0;
      padding-left: 12%;
      padding-right: 35px;
      width: 100vw;
      background-color: #f6f6f6;
    }

    @media (max-width: (320px)) {
      column-gap: 0;
    }
  }

  @include small-tablet {
    gap: 35px;
  }


  &__item {

    &:nth-child(3) {
      @media (max-width: (320px)) {
        width: 113px;
      }
    }
  }

  &__link {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    color: var(--grey);
    transition: background-color .5s ease-in-out, color .5s ease-in-out;

    &:focus {
      @include focus;
    }


    @include hover {
      color: #A65CF0;
    }


    &:active {
      @include active;
    }
  }

  @include tablet {
    margin-left: -55%;
    padding-left: 55%;
    min-height: 50px;
  }

  @media (max-width: (480px)) {
    display: block;
  }
}

.nav {
  grid-row: 2;
  grid-column: 4 / 9 span;

  @include tablet {
    grid-column: 3 / 7 span;
  }

  @media (max-width: (970px)) {
    grid-column: 9 span;
    grid-row: 3;
    margin-bottom: 34px;
  }

  @include small-tablet {
    grid-column: 6 span;
    margin-bottom: 30px;
  }

  @media (max-width: (320px)) {
    margin-bottom: 0px;
    background-image: none;
  }
}

.nav-menu {

  &__list {
    display: flex;
    justify-content: space-between;

    @include tablet {
      justify-content: end;
      gap: 5.8%;
    }

    @media (max-width: (970px)) {
      justify-content: flex-start;
    }

    @include small-tablet {
      gap: 3.8%;
      overflow-x: scroll;
      justify-content: space-between;
      white-space: nowrap;
      height: 100%;
    }

    @media (max-width: (320px)) {
      gap: 10%;
    }
  }

  &__item:nth-child(3) {
    @include mobile {
      margin-bottom: 0;
    }
  }

  &__link {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: var(--dark);
    transition: background-color .5s ease-in-out, color .5s ease-in-out;

    &:focus {
      @include focus;
    }


    @include hover {
      color: #A65CF0;
    }


    &:active {
      @include active;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  @include small-tablet {
    height: 27px;
  }
}

.menu--active {
  opacity: 1;
  z-index: 10;
}
