.high-rating {
  padding: 70px 0;

  &__title {
    margin: 0;
    margin-bottom: 26px;

    @media (max-width: (320px)) {
      margin-bottom: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;

    @include tablet {
      grid-template-columns: repeat(9, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }

    @media (max-width: (480px)) {
      gap: 16px;
    }
  }

  &__item {

    &:nth-child(1) {
      grid-row: 1;
      grid-column: 3 span;
      padding-top: 109px;

      @include small-tablet {
        padding-top: 112px;
      }

      @media (max-width: (320px)) {
        padding-top: 54px;
      }
    }

    &:nth-child(2) {
      grid-row: 1;
      grid-column: 4 / 3 span;
      padding-top: 90px;

      @include small-tablet {
        padding-top: 93px;
      }

      @media (max-width: (320px)) {
        padding-top: 49px;
      }
    }

    &:nth-child(3) {
      grid-row: 1;
      grid-column: 7 / 3 span;
      padding-top: 88px;

      @include small-tablet {
        grid-row: 2;
        grid-column: 3 span;
      }

      @media (max-width: (320px)) {
        padding-top: 49px;
      }
    }

    &:nth-child(4) {
      grid-row: 1;
      grid-column: 10 / 3 span;
      padding-top: 66px;

      @include tablet {
        grid-row: 2;
        grid-column: 3 span;
      }

      @include small-tablet {
        grid-column: 4/3 span;
      }

      @media (max-width: (320px)) {
        padding-top: 44px;
      }
    }

    &:nth-child(5) {
      grid-row: 2;
      grid-column: 3 span;
      padding-top: 74px;

      @include tablet {
        grid-column: 4 / 3 span;
      }

      @include small-tablet {
        grid-row: 3;
        grid-column: 3 span;
      }

      @media (max-width: (320px)) {
        padding-top: 46px;
      }
    }

    &:nth-child(6) {
      grid-row: 2;
      grid-column: 4 / 3 span;
      padding-top: 74px;

      @include tablet {
        grid-column: 7 / 3 span;
      }

      @include small-tablet {
        grid-row: 3;
        grid-column: 4/3 span;
      }

      @media (max-width: (320px)) {
        padding-top: 46px;
      }
    }

    &:nth-child(7) {
      grid-row: 2;
      grid-column: 7 / 3 span;
      padding-top: 61px;

      @include tablet {
        display: none;
      }
    }

    &:nth-child(8) {
      grid-row: 2;
      grid-column: 10 / 3 span;
      padding-top: 67px;

      @include tablet {
        display: none;
      }
    }

    @media (max-width: (320px)) {
      padding-right: 16px;
      padding-bottom: 16px;
      padding-left: 16px;
    }
  }

  &__image {

    @include small-tablet {
      &_name_R94 {
        margin-bottom: 16px;
      }
    }

    @media (max-width: (320px)) {

      &_name_R94 {
        margin-bottom: 22px;
      }

      &_name_T75 {
        margin-bottom: 17px;
      }

      &_name_Y68 {
        margin-bottom: 12px;
      }
    }
  }

  &__content {
    @media (max-width: (320px)) {
      align-items: flex-start;
      width: 98%;
    }
  }

  &__subtitle {
    @media (max-width: (320px)) {
      margin-bottom: 16px;
    }
  }

  &__price {
    color: #666666;
  }

  &__btn {
    padding: 18px 34px;

    @media (max-width: (480px)) {
      padding: 12px 25px;
    }

    @media (max-width: (320px)) {
      padding: 10px 23px;
    }
  }

  @media (max-width: (320px)) {
    padding-top: 43px;
    padding-bottom: 69px;
  }
}
