.header {
  position: relative;
  margin-bottom: 32px;

  &__container {
    display: grid;
    grid-template-rows: 0.72fr 1.28fr 60px;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 32px;
    align-items: center;

    @include tablet {
      grid-template-rows: 1fr 0.8fr 81px;
      grid-template-columns: repeat(9, 1fr);
    }

    @media (max-width: (970px)) {
      position: relative;
      grid-template-rows: 1fr 0.8fr auto 81px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: 1fr 0.89fr 51px 72px;
    }

    @media (max-width: (480px)) {
      grid-row-gap: 16px;
    }

    @media (max-width: (320px)) {
      grid-template-rows: 60px 1fr 52px 74px;
      grid-column-gap: 16px;
      grid-row-gap: 0;
    }
  }

  &__submenu {
    grid-row: 1;
    grid-column: 12 span;
    display: flex;
    justify-content: space-between;
    margin-right: -55%;
    margin-left: -55%;
    padding-right: 55%;
    padding-left: 55%;
    height: 100%;
    background-color: #F6F6F6;

    @include tablet {
      grid-column: 7 span;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 0;
      padding-right: 0;
      padding-left: 0;
    }

    @media (max-width: (970px)) {
      grid-column: 9 span;
    }

    @include small-tablet {
      grid-column: 6 span;
      margin-right: 0;
    }

    @media (max-width: (480px)) {
      grid-row: 2;
      grid-column: 3 span;
    }

    @media (max-width: (320px)) {
      position: relative;
      grid-column: 3 span;
      justify-content: center;
      align-items: start;
      height: auto;
      min-height: 50px;
      background-color: #ffffff;

      &::after {
        content: "";
        position: absolute;
        top: 55px;
        margin-left: -55%;
        border: 1px solid #f6f6f6;
      }
    }
  }

  &__wrap-phone {
    display: flex;

    @include tablet {
      width: 100%;
      height: 58px;
      background-color: var(--light);
    }
  }

  &__phone-link {
    position: relative;
    align-self: center;
    padding-left: 22px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    color: var(--secondary-background);
    background-color: var(--grey_light-background);
    letter-spacing: 0.6px;
    transition: color .4s ease-in-out;

    & svg {
      position: absolute;
      top: -6px;
      left: -1px;
      width: 22px;
      height: 22px;

      @include tablet {
        top: -3px;
      }

      @media (max-width: (320px)) {
        top: -4px;
        left: 0;
      }
    }

    &:focus {
      outline: none;
      background-color: #FFF5D1;
      color: var(--secondary-background);
    }

    &:focus svg > use {
      fill: #FF862F;
    }

    &:hover {
      color: #FFBD59;
    }

    &:hover svg > use {
      fill: #FFBD59;
    }

    &:active {
      color: #BB370E;
    }

    &:active svg > use {
      fill: #BB370E;
    }

    @include tablet {
      background-color: var(--light);
    }

    @media (max-width: (320px)) {
      padding-top: 0;
      padding-left: 18px;
      width: auto;
      height: auto;
    }
  }

  &__phone-link svg > use {
    fill: #FF862F;
    transition: fill .4s ease-in-out;
  }

  &__nav {

    @include tablet {
      display: flex;
    }

    @media (max-width: (480px)) {
      display: none;
    }

    @media (max-width: (320px)) {
      padding-left: 4%;
    }
  }

  &__logo {
    @include flex-v-center;
    grid-row: 2;
    grid-column: 2 span;
    width: 187px;
    height: 91px;

    &:focus {
      outline: none;
    }

    @include small-tablet {
      margin-left: -4px;
      width: 100%;
    }

    @media (max-width: (480px)) {
      grid-row: 1;
      grid-column: 6 span;
      justify-content: center;
      margin-left: -55%;
      margin-right: -55%;
      width: auto;
      height: 59px;
      background-color: #f6f6f6;
    }

    @media (max-width: (320px)) {
      position: relative;
      margin-bottom: auto;
      height: 50px;

      &::after {
        content: "";
        position: absolute;
        top: 66px;
        border: 1px solid #f6f6f6;
        width: 100%;

      }
    }

  }

  &__burger {
    visibility: hidden;

    @include mobile {
      visibility: visible;
      margin-top: 12px;
    }
  }

  &__block-search {
    grid-row: 3;
    grid-column: 10 span;
    align-self: start;
    position: relative;
    width: 100%;

    @include tablet {
      grid-column: 7 span;
      width: 99.9%;
    }

    @media (max-width: (970px)) {
      grid-row: 4;
    }

    @include small-tablet {
      grid-column: 6 span;
      z-index: 2;
    }
  }

  &__search-btn {
    position: absolute;
    top: 20px;
    left: 32px;
    width: 20px;
    height: 20px;

    & svg {
      width: 100%;
      height: 100%;
    }

    &:focus svg > use {
      outline: none;
    }

    &:active svg > use {
      fill: #A65CF0;
    }

    @include small-tablet {
      top: 17px;
      left: 28px;
    }

    @media (max-width: (320px)) {
      top: 19px;
      left: 31px;
    }
  }

  &__search-btn svg > use {
    fill: #999999;
  }

  &__search-link {
    position: absolute;
    top: 14px;
    right: 32px;
    width: 32px;
    height: 32px;

    & svg {
      width: 100%;
      height: 100%;
    }

    &:focus svg > use {
      stroke: #A65CF0;
      color: #FFFFFF;
      --fill_none: #C998FB;
    }

    &:hover svg > use {
      --fill_none: #A65CF0;
      color: #FFFFFF;
    }

    &:active svg > use {
      --stroke: #7033AC;
      color: #FFFFFF;
      --fill_none: #7033AC;
    }

    &:disabled svg > use {
      color: #999999;
      stroke: #7033AC;
    }

    @include small-tablet {
      top: 11px;
      right: 27px;
    }

    @media (max-width: (320px)) {
      top: 13px;
      right: 32px;
    }

    & svg > use {
      --stroke: #A65CF0;
      color: #A65CF0;
      --fill_none: none;
      transition: color .4s ease-in-out;
    }
  }

  &__input {
    padding-left: 59px;
    border-radius: 10px;
    border: none;
    width: 100%;
    height: 60px;
    background-color: #f6f6f6;
    transition: all 0.4s ease-in-out;

    &:focus {
      outline: 2px solid #A65CF0;
      background-color: #F8EFFF;
    }

    @include hover {
      background-color: #EAEAEA;
    }

    &:active {
      color: #333333;
      background-color: #f6f6f6;
    }

    @include small-tablet {
      padding-left: 55px;
    }

    @media (max-width: (320px)) {
      padding-left: 59px;
    }
  }

  &__block-user {
    grid-row: 3;
    grid-column: 11 / 2 span;
    gap: 29px;
    display: flex;
    justify-content: end;

    @include tablet {
      grid-row: 1;
      grid-column: 8 / 2 span;
      align-self: self-end;
      height: 41px;
    }

    @media (max-width: (970px)) {
      grid-row: 2;
      align-self: center;
    }

    @include small-tablet {
      grid-column: 5/2 span;
      gap: 33px;
      height: 32px;
    }

    @media (max-width: (480px)) {
      grid-column: 4/3 span;
      align-items: center;
      height: auto;
    }

    @media (max-width: (320px)) {
      gap: 30px;
      padding-top: 0;
    }
  }

  &__user {
    width: 29px;
    height: 31px;

    & svg {
      width: 100%;
      height: 100%;
    }

    &:focus svg > use {
      outline: none;
      color: #FFFFFF;
      --fill_user: #C998FB;
    }

    &:hover svg > use {
      color: #C998FB;
    }

    &:active svg > use {
      color: #7033AC;
      --fill_user: transparent;
    }

    @include small-tablet {
      width: 26px;
      height: 28px;
    }
  }

  &__user svg > use {
    color: #A65CF0;
    transition: color .4s ease-in-out;

    @include tablet {
      --fill_user: #F6F6F6;
    }

    @media (max-width: (970px)) {
      --fill_user: var(--light);
    }
  }

  &__basket {
    width: 44px;
    height: 36px;

    & svg {
      width: 100%;
      height: 100%;
    }

    &:focus svg > use {
      outline: none;
      color: #FFFFFF;
      --fill_user: #C998FB;
    }

    &:hover svg > use {
      color: #C998FB;
    }

    &:active svg > use {
      color: #7033AC;
      --fill_user: transparent;
    }

    @include small-tablet {
      width: 44px;
      height: 28px;
    }

    @media (max-width: (480px)) {
      height: 31px;
    }

    @media (max-width: (320px)) {
      width: 44px;
    }
  }

  &__basket svg > use {
    color: #A65CF0;
    transition: color .4s ease-in-out;

    @include tablet {
      --fill_user: #F6F6F6;
    }

    @media (max-width: (970px)) {
      --fill_user: var(--light);
    }
  }

  @include tablet {
    margin-bottom: 12px;
  }

  @include small-tablet {
    margin-bottom: 17px;
  }

  @media (max-width: (320px)) {
    margin-bottom: 14px;

    &::after {
      content: "";
      position: absolute;
      top: 115px;
      border: 1px solid #f6f6f6;
      width: 100%;
    }
  }
}

.header--secondary {
  margin-bottom: 19px;

  @include tablet {
    margin: 0;
  }
}

.focus-visible {
  outline: none;
}

