// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "OpenSans", sans-serif;
  --second-family: "PlayfairDisplay", sans-serif;
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --gap-high-rating: 32px;
  --container-offset-1024: 36px;
  // --container-offset-768: 72px;

  // colors
  --light: #fff;
  --dark: #333333;
  --gold: #D5B986;
  --grey: #999999;
  --grey_light-background: #F6F6F6;
  --grey_price: #666666;
  --md_grey-footer: #EAEAEA;
  --primary-btn: #A65CF0;
  --secondary-background: #FF862F;
  --primary_light: #C998FB;

  // color_svg
  --fill_user: #FFFFFF;
  --stroke_user: #A65CF0;
  --fill_none: none;
  --stroke_grey: #999999;
}
