.hero-bottom {
  padding-top: 154px;
  padding-bottom: 171px;
  background-image: url('../img/oxford.webp');
  background-repeat: no-repeat;
  background-size: cover;

  @include tablet {
    background-image: url('../img/oxford-1024.webp');
  }

  @include small-tablet {
    padding-top: 157px;
    padding-bottom: 175px;
    background-position: -7px -4px;
  }

  @media (max-width: (320px)) {
    margin-bottom: 92px;
    padding: 33px 0;
    height: 210px;
    background-image: url("../img/oxford-320.webp");
    background-position: 0px 1px;
  }

  &__container {
    @include small-tablet {
      padding-left: 36px;
    }

    @media (max-width: (320px)) {
      padding-left: 12px;
    }
  }

  &__descr {
    width: 508px;

    @include mobile {
      width: 97%;
    }

    @media (max-width: (320px)) {
      width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 69px;
    color: var(--light);
    font-size: 48px;
    font-style: normal;
    font-weight: 400;
    line-height: 121%;

    @include mobile {
      font-size: 32px;
    }

    @media (max-width: (320px)) {
      width: 77%;
      font-size: 16px;
    }
  }


  &__heading {
    color: var(--gold);
    font-family: var(--second-family);
    font-size: 60px;
    font-style: normal;
    font-weight: 900;
    line-height: 120%;

    @include mobile {
      display: block;
      font-size: 40px;
    }

    @media (max-width: (320px)) {
      font-size: 24px;
    }
  }

  &__btn {
    padding: 19px 32px;
    color: var(--light);
    font-weight: 700;

    @media (max-width: (320px)) {
      position: relative;
      top: 73px;
      display: block;
      padding: 21px 50px;
      width: 100%;
      text-align: center;
    }
  }
}
