.top {
  padding-top: 69px;

  &__title {
    margin: 0;
    margin-bottom: 27px;
  }

  &__list {
    display: grid;
    grid-template-rows: 54% 42.7%;
    grid-template-columns: repeat(12, 1fr);
    gap: 31px;
    height: 1px;
    min-height: 948px;

    @include tablet {
      grid-template-columns: repeat(9, 1fr);
      grid-template-rows: 1fr 1fr;
      min-height: 1054px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: 1fr 1fr 1fr;
      min-height: 1597px;
    }

    @include mobile {
      min-height: 1259px;
    }

    @media (max-width: (480px)) {
      gap: 16px;
    }

    @media (max-width: (320px)) {
      grid-template-rows: 1.34fr 0.83fr 0.83fr;
      row-gap: 32px;
      column-gap: 16px;
      min-height: 808px;
    }
  }

  &__card-link {
    @include mobile {
      display: none;
    }
  }

  &__card {
    display: grid;
    grid-template-rows: 6.9% 14% auto 4.8%;
    position: relative;
    padding-top: 27px;
    padding-bottom: 30px;
    border-radius: 10px;
    background: var(--grey_light, #F6F6F6);

    &:nth-child(1) {
      grid-column: 6 span;
      grid-row: 1;

      @include tablet {
        grid-template-rows: 6.9% 14% auto 4.5%;
      }

      @media (max-width: (320px)) {
        grid-template-rows: 6.9% 14.9% 73%;
        padding-bottom: 0;
      }
    }

    &:nth-child(2) {
      grid-column: 6 span;
      grid-row: 1;

      @include tablet {
        grid-column: 4 / 3 span;
        grid-row: 2;
      }

      @media (max-width: (320px)) {
        grid-template-rows: 15.9% 13% 63%;
        padding-top: 11px;
      }
    }

    &:nth-child(3) {
      grid-template-rows: 9% 18% 67% 4.8%;
      grid-column: 4 span;
      grid-row: 2;

      @include tablet {
        grid-column: 7/3 span;
        grid-row: 1;
        grid-template-rows: 6.9% 15% auto 4.5%;
      }

      @include small-tablet {
        grid-column: 3 span;
        grid-row: 2;
      }

      @media (max-width: (575px)) {
        grid-template-rows: 10.9% 15% auto 5.5%;
      }

      @media (max-width: (320px)) {
        grid-template-rows: 15% 14% 63%;
        padding-top: 11px;
      }
    }

    &:nth-child(4) {
      grid-template-rows: 9% 18% 67% 4.8%;
      grid-column: 5 / 4 span;
      grid-row: 2;

      @include tablet {
        grid-column: 3 span;
        grid-template-rows: 7% 18% 71% 8.8%;
      }

      @include small-tablet {
        grid-column: 3 span;
        grid-row: 3;
        grid-template-rows: 7% 15% 73% 4.8%;
      }

      @media (max-width: (320px)) {
        grid-template-rows: 21.5% 14% 58%;
        padding-top: 10px;
      }
    }

    &:nth-child(5) {
      grid-template-rows: 9% 18% 67% 4.8%;
      grid-column: 9 / 4 span;
      grid-row: 2;

      @include tablet {
        grid-column: 7/3 span;
        grid-template-rows: 7% 16% 73% 4.8%;
      }

      @include small-tablet {
        grid-column: 4/3 span;
        grid-row: 3;
        grid-template-rows: 7% 15% 73% 4.8%;
      }

      @media (max-width: (320px)) {
        grid-template-rows: 16% 11% 67%;
        padding-top: 12px;
      }
    }

    @include tablet {
      grid-template-rows: 6.9% 15% auto 4.8%;
    }

    @include mobile {
      grid-template-rows: 10.9% 15% auto 4.8%;
    }

    @media (max-width: (320px)) {
      padding-bottom: 0;
    }
  }

  &__views-list {
    display: flex;
    gap: 29px;
    padding-left: 30px;

    @media (max-width: (970px)) {
      padding-left: 13px;
    }

    @include small-tablet {
      flex-wrap: wrap;
      gap: 10.7%;
      padding-left: 30px;
    }

    @include mobile {
      gap: 0;
      flex-direction: column;
      padding-left: 16px;
      width: 100%;
    }

    @media (max-width: (320px)) {
      flex-wrap: nowrap;
    }

    &--offset {
      gap: 44px;

      @media (max-width: (970px)) {
        gap: 10%;
      }

      @include small-tablet {
        gap: 16%;
      }

      @include mobile {
        gap: 0;
      }
    }

    &_row {
      flex-direction: row;
      gap: 30px;

      @media (max-width: (480px)) {
        gap: 20px;
      }

      @media (max-width: (320px)) {
        gap: 20px;
        padding-left: 30px;
      }
    }

    @media (max-width: (320px)) {
      font-size: 13px;
    }
  }

  &__item-link {
    color: var(--grey);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;

    @media (max-width: (970px)) {
      font-size: 12px;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__item-link_size_s {
    @media (max-width: (320px)) {
      font-size: 10px;
    }
  }

  &__subtitle {
    margin: 0;
    padding-left: 30px;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @media (max-width: (970px)) {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      align-self: center;
      padding-left: 16px;
      font-size: 16px;
    }

    @media (max-width: (320px)) {
      line-height: 44%;
    }
  }

  &__subtitle_size_xxl {
    @media (max-width: (320px)) {
      padding-left: 30px;
      font-size: 24px;
    }
  }

  &__subtitle-link {
    display: none;

    @include mobile {
      display: inline-block;
      padding-top: 5px;
      width: 22px;
      height: 22px;
    }
  }

  &__image {

    @include mobile {
      align-self: self-end;
    }
  }

  @include small-tablet {
    padding-top: 65px;
  }

  @media (max-width: (320px)) {
    padding-top: 70px;
  }
}
