.contacts {
  padding: 70px 0;

  &__container {
    @include flex-all-sb;
    height: 100%;
  }

  &__block-inputs {
    display: flex;
    flex-direction: column;
    width: 48.75%;

    @include tablet {
      width: 42.6%;
    }

    @include small-tablet {
      width: 65.1%;
    }

    @media (max-width: (480px)) {
      width: 100%;
    }
  }

  &__custom-checkbox {
    width: 100%;
  }

  &__title {
    margin-bottom: 9px;
  }

  &__text {
    margin: 0;
    margin-bottom: 16px;
    color: var(--grey_price);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__image {
    width: 48.75%;
    height: 100%;
    background-image: url('/img/contacts.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-size: contain;
    background-position: 47% 49%;

    @include tablet {
      width: 54%;
    }

    @include small-tablet {
      width: 30.1%;
      background-image: url('/img/contacts2.webp');
    }

    @media (max-width: (480px)) {
      display: none;
    }
  }

  @media (max-width: (320px)) {
    padding-top: 45px;
    padding-bottom: 68px;
  }
}

.form {
  display: flex;
  flex-direction: column;

  &__label {
    display: flex;
    flex-direction:column-reverse;
    margin-bottom: 30px;
  }

  &__input {
    padding-left: 18px;
    border-radius: 10px;
    width: 100%;
    height: 54px;
    color: var(--grey, #999);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    background-color: var(--grey_light-background);
    transition: all 0.4s ease-in-out;

    &:focus {
      outline: 1px solid #A65CF0;
      background-color: #F8EFFF;
    }

    @include hover {
      background-color: #EAEAEA;
    }

    &:active {
      color: #333333;
      background-color: #f6f6f6;
      outline: 1px solid #B8EC64;
    }
  }

  &__btn {
    align-self: start;
    padding: 19px 35px;
    margin-bottom: 13px;
    color: var(--light);
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;

    @media (max-width: (320px)) {
      margin-bottom: 12px;
    }
  }
}

.input-chekbox [type=checkbox]:checked {
  background-color: #A65CF0;
}

.custom-checkbox {
  @include custom-checkbox;

  &__link {
    color: var(--primary-btn);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration-line: underline;

    @media (max-width: (320px)) {
      font-size: 12px;
    }
  }
}

.custom-checkbox + .just-validate-error-label {
  font-size: 10px;
  padding-top: 3px;
}

.just-validate-error-label {
  margin-bottom: 5px;
}
