.catalog {
  margin-bottom: 70px;

  &__container {

    @include small-tablet {
      padding-top: 10px;
    }

    @media (max-width: (320px)) {
      padding-top: 11px;
    }
  }

  &__breadcrums {
    margin-bottom: 24px;
  }

  &__dropdown {
    display: flex;
  }

  &__products {

    grid-row: 2;
    grid-column: 4/9 span;
    padding-top: 24px;

    @include tablet {
      grid-row: 3;
      grid-column: 9 span;
    }

    @include small-tablet {
      padding-top: 23px;
    }

    @media (max-width: (320px)) {
      grid-column: 6 span;
      padding-top: 32px;
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0fr 2fr;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 32px;

    @include tablet {
      grid-template-rows: 0.071fr 0.1fr 1.8fr;
      grid-template-columns: repeat(9, 1fr);
    }

    @media (max-width: (320px)) {
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: 0.1fr 0.1fr 1.8fr;
      column-gap: 16px;
    }
  }

  &__heading {
    grid-row: 1;
    grid-column: 4/9 span;
    margin: 0;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;

    @include tablet {
      grid-column: 9 span;
    }

    @media (max-width: (320px)) {
      grid-column: 6 span;
    }
  }

  &__block-cards {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(9, 1fr);
    gap: 32px;
    margin-bottom: 32px;

    @include tablet {
      margin-bottom: 36px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      margin-bottom: 38px;
    }

    @media (max-width: (320px)) {
      gap: 16px;
      margin-bottom: 35px;
    }
  }

  &__item {
    padding-bottom: 32px;

    &:nth-child(1) {
      grid-row: 1;
      grid-column: 3 span;
      padding-top: 109px;

      @media (max-width: (320px)) {
        padding-top: 52px;
      }
    }

    &:nth-child(2) {
      grid-row: 1;
      grid-column: 4 / 3 span;
      padding-top: 90px;

      @media (max-width: (320px)) {
        padding-top: 49px;
      }
    }

    &:nth-child(3) {
      grid-row: 1;
      grid-column: 7 / 3 span;
      padding-top: 88px;

      @include small-tablet {
        grid-row: 2;
        grid-column: 4 / 3 span;
      }

      @media (max-width: (320px)) {
        grid-column: 3 span;
        padding-top: 50px;
      }
    }

    &:nth-child(4) {
      grid-row: 2;
      grid-column: 3 span;
      padding-top: 66px;

      @include tablet {
        padding-top: 68px;
      }

      @media (max-width: (320px)) {
        grid-column: 4/3 span;
        padding-top: 44px;
      }
    }

    &:nth-child(5) {
      grid-row: 2;
      grid-column: 4 / 3 span;
      padding-top: 74px;

      @include small-tablet {
        grid-row: 3;
        grid-column: 3 span;
        padding-top: 77px;
      }

      @media (max-width: (320px)) {
        padding-top: 47px;
      }
    }

    &:nth-child(6) {
      grid-row: 2;
      grid-column: 7 / 3 span;
      padding-top: 74px;

      @include small-tablet {
        grid-row: 3;
        grid-column: 4/3 span;
      }

      @media (max-width: (320px)) {
        padding-top: 47px;
      }
    }

    &:nth-child(7) {
      grid-row: 3;
      grid-column: 3 span;
      padding-top: 61px;
    }

    &:nth-child(8) {
      grid-row: 3;
      grid-column: 4 / 3 span;
      padding-top: 67px;

      @include small-tablet {
        display: none;
      }
    }

    &:nth-child(9) {
      grid-row: 3;
      grid-column: 7 / 3 span;
      padding-top: 66px;
    }

    @include tablet {
      padding-bottom: 29px;
    }

    @media (max-width: (320px)) {
      padding-right: 16px;
      padding-bottom: 16px;
      padding-left: 16px;
    }
  }

  &__image {

    @media (max-width: (320px)) {
      margin-bottom: 21px;
    }

    &_name_T75 {
      @media (max-width: (320px)) {
        margin-bottom: 17px;
      }
    }

    &_name_D31 {
      @media (max-width: (320px)) {
        margin-bottom: 17px;
      }
    }

    &_name_Y68 {
      @media (max-width: (320px)) {
        margin-bottom: 11px;
      }
    }

    &_name_W95 {
      @media (max-width: (320px)) {
        margin-bottom: 14px;
      }
    }

    &_name_G41 {
      @media (max-width: (320px)) {
        margin-bottom: 14px;
      }
    }
  }

  &__descr {

    @media (max-width: (320px)) {
      margin-bottom: 16px;
      width: 97%;
    }
  }

  &__btn {
    padding: 17px 35px;

    @media (max-width: (480px)) {
      padding: 12px 25px;
    }

    @media (max-width: (320px)) {
      padding: 10px 23px;
    }
  }

  &__subtitle {

    @media (max-width: (320px)) {
      margin-bottom: 18px;
    }
  }

  &__block-tabs {
    @include flex-all-center;
    gap: 16px;
  }

  &__tab {
    padding: 0;
    border-radius: 10px;
    width: 32px;
    height: 32px;

    &:nth-child(3) {
      display: none;

      @include small-tablet {
        display: block;
      }
    }
  }

  @include tablet {
    margin-bottom: 66px;
  }

  @include small-tablet {
    margin-bottom: 73px;
  }

  @media (max-width: (320px)) {
    margin-bottom: 70px;
  }
}

.catalog-inputs {

  &__block-filters {
    display: flex;
    flex-direction: column;
    padding-bottom: 64px;
    grid-column: 3 span;
    grid-row: 1 / 2 span;

    @include tablet {
      grid-column: 9 span;
      grid-row: 2;
      flex-direction: row;
      padding-bottom: 0;
    }

    @include mobile {
      z-index: 2;
      flex-wrap: wrap;
      row-gap: 16px;
    }

    @media (max-width: (320px)) {
      grid-column: 6 span;
      row-gap: 18px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    color: var(--dark);
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;

    @include tablet {
      display: none;
    }
  }

  &__filter {
    display: flex;
    flex-direction: column;
    margin-bottom: 27px;
    width: 100%;

    @include tablet {
      flex-grow: 1;
      margin-bottom: 0;
      width: 0;
    }

    @include small-tablet {
      flex-grow: 1.33;
    }

    @include mobile {
      width: 50%;
    }

    &-color {

      @media (max-width: (320px)) {
        width: 55%;
      }
    }

    &-category {

      @media (max-width: (320px)) {
        width: 45%;
      }
    }

    &-price {

      @include tablet {
        flex-grow: 3.34;
      }

      @media (max-width: (970px)) {
        flex-grow: 2;
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 10px;
    color: var(--grey_price);
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 130%;

    &--offset {
      margin-bottom: 5px;

      @media (max-width: (320px)) {
        margin-bottom: 3px;
      }
    }
  }

  &__input-price {
    padding-left: 14px;
    border-radius: 2px;
    border: 1px solid var(--grey);
    width: 86px;
    height: 30px;
    color: #666666;
    transition: all 0.4s ease-in-out;

    &:focus {
      outline: none;
      border: 1px solid var(--primary-btn);
      background-color: #EBD1FF;
    }

    @include hover {
      border: 1px solid var(--primary-btn);
    }

    &:active {
      background-color: transparent;
      border: 1px solid #7033AC;
    }

    @include small-tablet {
      padding-left: 12px;
    }
  }

  &__custom-checkbox {
    &:not(:last-child) {
      margin-bottom: 11px;
    }
  }

  &__checkbox-color {
    padding-left: 32px;
    color: var(--grey_price);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;

    &::before {
      width: 20px;
      height: 20px;
    }

    &::after {
      width: 20px;
      height: 20px;
      background-size: 20px 20px;
    }
  }

  &__oxford {
    margin-top: auto;

    @include tablet {
      display: none;
    }
  }

  &__image {
    display: block;
  }

  &__collection {
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 30px;
    border-radius: 0px 0px 10px 10px;
    background: var(--black, #333);
  }

  &__name {
    margin: 0;
    margin-bottom: 15px;
    width: 67%;
    color: var(--grey);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__name-oxford {
    color: var(--gold);
    font-family: var(--second-family);
    font-size: 24px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
  }

  &__link {
    display: block;
    padding-left: 0;

    &::after {
      left: 0;
    }

    svg {
      left: 84px;
    }
  }
}



