.similar {
  padding-bottom: 70px;

  &__title {
    margin: 0;
    margin-bottom: 24px;
    color: var(--dark);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%;
  }

  &__list {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;

    @include tablet {
      grid-template-columns: repeat(9, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }

    @media (max-width: (320px)) {
      gap: 16px;
    }
  }

  &__item {

    &:nth-child(1) {
      grid-column: 3 span;
      padding-top: 86px;

      @media (max-width: (320px)) {
        padding-top: 46px;
      }
    }

    &:nth-child(2) {
      grid-column: 4 / 3 span;
      padding-top: 58px;

      @media (max-width: (320px)) {
        padding-top: 38px;
      }
    }

    &:nth-child(3) {
      grid-column: 7 / 3 span;
      padding-top: 86px;

      @include small-tablet {
        display: none;
      }
    }

    &:nth-child(4) {
      grid-column: 10 / 3 span;
      padding-top: 61px;

      @include tablet {
        display: none;
      }
    }

    @media (max-width: (320px)) {
      padding-right: 16px;
      padding-bottom: 16px;
      padding-left: 16px;
    }
  }

  &__image {

    &_name_H58 {
      margin-bottom: 16px;

      @media (max-width: (320px)) {
        margin-bottom: 7px;
      }
    }
  }

  &__descr {

    @include tablet {
      margin-bottom: 22px;
    }

    @media (max-width: (320px)) {
      margin-bottom: 15px;
      width: 77%;
    }
  }

  &__subtitle {

    @media (max-width: (320px)) {
      margin-bottom: 18px;
    }
  }

  &__price {

    @media (max-width: (320px)) {
      margin-right: 0;
    }
  }

  &__btn {

    @media (max-width: (480px)) {
      padding: 12px 25px;
    }

    @media (max-width: (320px)) {
      padding: 11px 23px;
    }
  }

  @include tablet {
    padding-bottom: 69px;
  }

  @include small-tablet {
    padding-bottom: 73px;
  }
}
