.breadcrums {
  display: flex;
  gap: 14px;
  margin-bottom: 30px;

  &__item {
    position: relative;

    &:not(:last-child) {
      &::after {
        content: "/";
        position: absolute;
        left: 59px;
        color: var(--grey);
      }
    }

    &:first-child {
      &::after {
        left: 62px;
      }
    }
  }

  &__link {
    color: var(--grey);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
  }

  @media (max-width: (480px)) {
    display: none;
  }
}

.breadcrums__item:last-child>.breadcrums__link {
  color: #7033AC;
}
