.hero-top {
  padding: 80px 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/discount.webp");


  @include tablet {
    background-image: url("../img/discount-1024.webp");
  }

  @include small-tablet {
    padding: 82px 0;
    background-image: url("../img/discount-768.webp");
  }

  @media (max-width: (320px)) {
    margin-bottom: 92px;
    padding: 33px 0;
    height: 210px;
    background-image: url("../img/discount-320.webp");
  }

  &__title {
    margin: 0;
    margin-bottom: 49px;
    max-width: 496px;
    color: var(--grey);
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;



    @media (max-width: (320px)) {
      padding-left: 18px;
      font-size: 20px;
    }
  }

  &__btn {
    padding: 21px 50px;
    color: var(--light);
    font-weight: 700;

    @media (max-width: (320px)) {
      position: relative;
      top: 108px;
      padding: 21px 50px;
      width: 100%;
    }
  }
}
