
.swiper-button-disabled {
  cursor: default;
}

.swiper-initialized {
  height: 100%;
  overflow: hidden;

  @media (max-width: (575px)) {
    align-self: center;
    width: 296px;
  }
}

.swiper-wrapper {
  @media (max-width: (575px)) {
    flex-direction: column;
    align-items: center;
  }
}

.swiper-slide {
  @media (max-width: (575px)) {
    width: 100%;
    margin-bottom: 24px;
  }
}

.swiper-slide {
  box-sizing: border-box;
  border-radius: 10px;
  text-align: center;
  background-color: var(--grey_light-background);
}

