.btn-primary {
  padding: 22px 47px;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  background: var(--primary-btn);
  transition: all .4s ease-in-out;

  &:focus {
    outline: 2px solid #7033AC;
    background-color: #C998FB;
  }

  @include hover {
    outline: none;
    background-color: #C998FB;
  }

  &:active {
    background-color: #7033AC;
  }

  &:disabled {
    background-color: #EAEAEA;
    color: var(--grey);
  }
}

.btn-secondary {
  z-index: 2;
  padding: 19px 35px;
  border-radius: 10px;
  outline: 2px solid #A65CF0;
  color: var(--primary-btn);
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%;
  transition: all .4s ease-in-out;

  &:focus {
    outline: 2px solid #A65CF0 !important;
    background-color: #C998FB;
    color: #FFFFFF;
  }

  @include hover {
    background-color: #A65CF0;
    color: #FFFFFF;
  }

  &:active {
    outline: 2px solid #7033AC;
    background-color: #7033AC;
    color: #FFFFFF;
  }
}

.btn-arrows {
  width: 32px;
  height: 32px;

    & svg {
      width: 100%;
      height: 100%;
      --stroke_grey: #A65CF0;
      color: #A65CF0;
    }

    &:focus svg > use {
      --stroke_grey: #A65CF0;
      color: #FFFFFF;
      --fill_none: #C998FB;
    }

    &:hover svg > use {
      --fill_none: #A65CF0;
      color: #FFFFFF;
    }

    &:active svg > use {
      --stroke_grey: #7033AC;
      color: #FFFFFF;
      --fill_none: #7033AC;
    }

    &:disabled svg > use {
      cursor: default;
      color: #999999;
      --stroke_grey: #999999;
      --fill_none: none;
    }
}

.btn-arrows svg > use {
  transition: color .4s ease-in-out;
}
